import React, { Component } from 'react'
import { NO_CAMERA_IMAGE } from '../../actions/api'
import ProductDetail from './productDetail'

export default class products extends Component { 
    state={detailProductData:[]}

      detailProductShow=(item)=>{
        this.setState({detailProductData:item},()=>this.props.hideProductDetail(true))
     } 

  render() {
    const { productDetail, hideProductDetail, productData} = this.props



    console.log("productData", productData)

    return (!productDetail ?
        <div className='lb_products'>
            {productData?.map((product,i)=>{
                return  <div className='lbproduct_thumb' onClick={()=> this.detailProductShow(product)}>
                <div className='lbproduct_image_holder'>
                    <img className='lbproduct_thumb_image' src={product?.product_image} alt='Post and Product' onError={(e) => e.target.src = NO_CAMERA_IMAGE} />
                </div>
                <div className='lbproduct_heading'>
                  {product?.product_title}
                </div>
                <div className='lbproduct_price'>
                    <div className='lbproduct_currency'>{product?.price_currency_symbol}</div>{product?.product_price}
                </div>
            </div>
            }) }
           
        </div>  : 
        <ProductDetail detailProductData={this.state.detailProductData}/>
    )
  }
}
