import React, { Component } from 'react'
import { NO_CAMERA_IMAGE } from '../../actions/api'
import PostDetail from './postDetail'

export default class post extends Component {
   state ={postDetail:false, detailPostData:[]}

   detailPostShow=(item)=>{
      this.setState({detailPostData:item},()=>this.props.hidePostDetail(true))
   }

  render() {
    const {postDetail, hidePostDetail, postData} = this.props
    return (!postDetail ?
        <div className='lb_posts'>
            {postData?.map((item,i)=>{
              return <div key={i}className='lb_image_holder' onClick={()=> this.detailPostShow(item)}>
                <img className='lb_PPimage' src={item?.postFileNew} alt='Post and Product' onError={(e) => e.target.src = NO_CAMERA_IMAGE}/>
              </div>
            })}
        </div>: 
         <PostDetail detailPostData={this.state.detailPostData}/>
    )
  }
}
