import React, { Component } from 'react'
import Post from './post'
import moment from 'moment';
import { NO_CAMERA_IMAGE } from '../../actions/api';
export default class postDetail extends Component {

  render() {
    const { detailPostData } = this.props
    console.log("detailPostData", detailPostData)
    return (
      detailPostData ? <div className='lbPost_detail'>
        <div className='lbPost_detail_inner'>
          <div className='lbPost_detail_image_holder'>
            <img className='lbPost_detail_image' src={detailPostData?.postFileNew} alt='Post and Product' onError={(e) => e.target.src = NO_CAMERA_IMAGE}/>
          </div>
          <div className='lbPost_detail_author_detail'>
            <div className='lbPost_detail_author_inner'>
              <div className='lbPost_detail_author_image_holder'>
                <img className='lbPost_detail_author_image' src={detailPostData?.author?.picture} alt='' onError={(e) => e.target.src = NO_CAMERA_IMAGE}/>
              </div>
              <div className='lbPost_detail_alignRight'>
                <div className='lbPost_detail_author_name'>
                  {detailPostData?.author?.name}
                </div>
                <div className='lbPost_detail_post_time'>
                  {moment(new Date(detailPostData?.createdAt * 1000)).fromNow()}
                </div>
              </div>
            </div>
            {detailPostData?.ugc_products?.map((product, i) => {
              return <div className='lbpost_detail_product' key={`product_${product?.id}_${i}`}>
                <div className='lbpost_detail_product_box'>
                  <div className='lbpost_detail_product_image_holder'>
                    <img className='lbpost_detail_product_image' src={`${product?.UgcProduct?.product_image}`} alt='' onError={(e) => e.target.src = NO_CAMERA_IMAGE}/>
                  </div>
                  <div className='lbpost_detail_product_content'>
                    <div className='lbpost_detail_product_title'>{product?.UgcProduct?.product_title}</div>
                    <div className='lbpost_detail_product_price'>{product?.UgcProduct?.price_currency_symbol}{Math.trunc((product?.UgcProduct?.product_price)*100)/100}</div>
                    <div className='lbpost_detail_product_button'>
                      <a href={product?.UgcProduct?.product_url} target="_blank" style={{ color:`#ffffff`}}>View on Website</a>
                      </div>
                  </div>
                </div>
              </div>
            }) }
            

          </div>
        </div>
        <Post />
      </div> : ''
    )
  }
}
