import { GET_SHOPON_POST_BYSLUG_REDUCER,  GET_SHOPON_PRODUCT_BYSLUG_REDUCER  } from './reducersKeys'
const initialState = { 
    shopOnPosts: [],
    shoponDetail:[],
    shopOnProducts: [],
}
export default function reducer(state = initialState, action) {
    switch (action.type) {
       
        case GET_SHOPON_POST_BYSLUG_REDUCER: {
            const { shoponDetail , postData } = action.payload
             shoponDetail['extraData'] =  shoponDetail?.extra_data ? JSON.parse(shoponDetail?.extra_data):{}
            return { ...state, shopOnPosts: postData, shoponDetail: shoponDetail}
        }

        case GET_SHOPON_PRODUCT_BYSLUG_REDUCER: { 
            return { ...state, shopOnProducts: action.payload }
        }

        
        default:
            return { ...state }
    }

}