import axios from 'axios';
import HttpClient from "./http-client";
import { GET_SHOPON_POST_BYSLUG_API, GET_SHOPON_PRODUCT_BYSLUG_API   } from './api'
import {  GET_SHOPON_PRODUCT_BYSLUG_REDUCER, GET_SHOPON_POST_BYSLUG_REDUCER  } from '../reducers/reducersKeys';
import { TOAST_DISMISS, TOAST_ERROR, TOAST_SUCCESS } from '../utils';




export const GET_SHOPON_USER_POST= () => { 
    const slug = window.location.pathname.split("/").pop()
    return (dispatch) => {
        new HttpClient().get(`${GET_SHOPON_POST_BYSLUG_API}/${slug}/40`).then((response) => {
            const { postData , response_code, shoponDetail } = response.data
            if (response_code == 200) {
                dispatch({ type: GET_SHOPON_POST_BYSLUG_REDUCER, payload: {shoponDetail, postData} });
            }
            dispatch(GET_SHOPON_USER_PRODUCT())
        }).catch(error => { 
            console.log("error", error)
        });
    }
}


export const GET_SHOPON_USER_PRODUCT= () => { 
    const slug = window.location.pathname.split("/").pop()
    return (dispatch) => {
        new HttpClient().get(`${GET_SHOPON_PRODUCT_BYSLUG_API}/${slug}`).then((response) => {
            const { responseData, responseCode, responseMessage } = response.data
            if (responseCode == 200) {
                dispatch({ type: GET_SHOPON_PRODUCT_BYSLUG_REDUCER, payload: responseData?.products});
            }
        }).catch(error => {
            console.log("error", error)
        });
    }
}